<template>
  <v-dialog v-model="dialog" persistent max-width="600px">
    <template v-slot:activator="{ on, attrs }">
      <!-- build button for add Section  -->
      <v-btn
        class="ma-5"
        outlined
        medium
        v-bind="attrs"
        v-on="on"
        color="primary"
      >
        <v-col cols="12"> <v-icon>mdi-plus</v-icon> add Section </v-col>
      </v-btn>
      <!-- end  build button for add Section  -->
    </template>
    <v-form ref="form" lazy-validation v-model="valid">
      <v-card>
        <!-- title card dialog  -->
        <v-card-title>
          <span class="text-h5">Add Section</span>
        </v-card-title>
        <!--  -->
        <v-card-text>
          <v-container>
            <v-row>
              <!-- Name field card  -->
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Section Name Ar *"
                  required
                  v-model="section.name.ar"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Section Name En *"
                  required
                  v-model="section.name.en"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Section Name Tr*"
                  required
                  v-model="section.name.tr"
                ></v-text-field>
              </v-col>
              <!--  -->

              <!-- Description field card  -->
              <v-col cols="12">
                <v-textarea
                  label="Section Description Ar*"
                  v-model="section.description.ar"
                  :rules="descriptionRules"
                >
                </v-textarea>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  label="Section Description En*"
                  v-model="section.description.en"
                  :rules="descriptionRules"
                >
                </v-textarea>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  label="Section Description Tr*"
                  v-model="section.description.tr"
                  :rules="descriptionRules"
                >
                </v-textarea>
              </v-col>
              <!--  -->
              <!-- selection field from areas  -->

              <v-col cols="12">
                <v-autocomplete
                  :rules="areasRules"
                  v-model="section.areas"
                  label="Implementation areas"
                  multiple
                  :items="allAreas"
                  item-text="name"
                  item-value="id"
                >
                  <template v-slot:selection="data">
                    <v-chip
                      class="primary"
                      v-bind="data.attrs"
                      :input-value="data.name"
                      close
                      @click="data.select"
                      @click:close="remove(data.item)"
                    >
                      {{ data.item.name }}
                    </v-chip>
                  </template>
                </v-autocomplete>
              </v-col>
              <!--end build  selection field from areas  -->

              <!-- selection field from areas  -->
              <build-nation-field v-on:changNation="changNation" />
              <!--end build  selection field from areas  -->

              <!-- build image field  -->
              <v-col cols="12">
                <v-file-input
                  :rules="imageRules"
                  v-model="file"
                  chips
                  accept="image/png, image/jpeg"
                  label="Image"
                  @change="onFileChange"
              /></v-col>
              <v-col cols="12">
                <v-img
                  contain
                  max-height="250"
                  aspect-ratio="1"
                  v-show="image"
                  :lazy-src="image"
                  :src="image"
                >
                </v-img>
              </v-col>
            </v-row>
          </v-container>
          <small color="red">*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialog = false"> Cancel </v-btn>
          <v-btn
            color="secondary"
            class="add-section-button"
            text
            @click="addSection"
          >
            <span v-if="!progressAddSection">Add Section</span>
            <v-progress-circular
              v-if="progressAddSection"
              color="primary"
              indeterminate
            ></v-progress-circular>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-form>
  </v-dialog>
</template>
<script>
import { sectionController } from "./section";
export default {
  computed: {
    lang() {
      return this.$store.getters.lang;
    },
  },
  data() {
    return {
      // form
      valid: true,
      // validation form
      nameRules: [(v) => !!v || "Name is required"],
      descriptionRules: [(v) => !!v || "Description is required"],
      imageRules: [(v) => !!v || "chose image ."],
      // image
      file: null,
      image: null,
      section: {
        name: {},
        description: {},
      },
      areasRules: [(v) => !!(v && v.length) || "Choose at least section"],

      //
      progressAddSection: false,
      isUpdating: false,
      dialog: false,
      allAreas: [],
      //  selectedAreas contains the id of areas from send this ids to server ..
    };
  },

  watch: {
    isUpdating(val) {
      if (val) {
        setTimeout(() => (this.isUpdating = false), 3000);
      }
    },
    dialog(state) {
      if (state) {
        this.getAreas();
      }
    },
  },

  methods: {
    getAreas: async function () {
      this.allAreas = this.$store.getters.areas;
      const result = this.allAreas.map((item) => {
        return {
          name: item.name[this.lang],
          id: item.id,
        };
      });

      this.allAreas = result;
      var arr = this.section.areas;
      if (arr) {
        var areas = [];
        arr.forEach((element) => {
          areas.push(element.id);
        });
        this.section.areas = areas;
      }
    },
    remove(item) {
      const index = this.section.areas.indexOf(item.id);
      if (index >= 0) this.section.areas.splice(index, 1);
    },
    changNation(nation) {
      this.section.nation_id = nation;
    },
    addSection: async function () {
      if (this.$refs.form.validate()) {
        this.progressAddSection = true;
        var state = await sectionController.addSection(this.file, this.section);
        if (state) {
          this.dialog = false;
          this.restData();
        } else {
          this.progressAddSection = false;
        }
      }
    },
    onFileChange(e) {
      if (e != null) {
        const reader = new FileReader();
        reader.readAsDataURL(this.file);
        reader.onload = (e) => {
          this.image = e.target.result;
        };
      } else {
        this.file = null;
        this.image = null;
      }
    },

    restData() {
      this.dialog = false;
      this.image = null;
      this.file = null;
      this.$refs.form.reset();
      this.section = {
        name: {},
        description: {},
      };
      this.progressAddSection = false;
      this.$emit("getVal");
    },
  },
  components: {
    "build-nation-field": () => import("../nation.vue"),
  },
};
</script>
 